html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-x: hidden;
  margin: 0;
  font-family: "ProximaNova", sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn--reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1140px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title {
  font-weight: 700;
  font-size: 70px;
  line-height: 79.78px;
  color: var(--color-white);
}

@media (max-width: 700px) {
  .title {
    font-size: 44px;
    line-height: 46px;
  }
}

.subtitle {
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  color: var(--color-white);
}

@media (max-width: 700px) {
  .subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
  }
}

.button {
  border: none;
  border-radius: 5px;
  width: 346px;
  height: 60px;
  font-weight: 700;
  font-size: 17px;
  line-height: 29px;
  color: var(--color-white);
  background-image: linear-gradient(-78deg, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  background-color: #ff40f8;
}

.button_smal {
  width: 100%;
}

@media (max-width: 425px) {
  .button {
    width: 100%;
  }
}

.input {
  outline: none;
  border: 1px solid #d6d6d6;
  border-radius: 5px;
  padding: 15px 26px;
  width: 346px;
  height: 60px;
  font-weight: 400;
  font-size: 16px;
  line-height: 29px;
  color: rgba(0, 0, 0, 0.5);
}

.input:focus {
  border-width: 1px;
  border-image: linear-gradient(-78deg, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  border-image-slice: 1;
  font-weight: 400;
  font-size: 16px;
  line-height: 29px;
  color: var(--color-black);
}

.input_smal {
  width: 100%;
  background: none;
}

.input_smal:focus {
  border-image: none;
}

@media (max-width: 425px) {
  .input {
    width: 100%;
  }
}

.section-title {
  font-weight: 700;
  font-size: 55px;
  line-height: 58px;
  color: var(--color-black);
}

.section-title_white {
  color: var(--color-white);
}

@media (max-width: 425px) {
  .section-title {
    font-size: 44px;
  }
}

@media (max-width: 1200px) {
  .is-desktop {
    display: none;
  }
}

@media (min-width: 425px) {
  .is-mobile {
    display: none;
  }
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation .menu {
  display: flex;
}

.navigation .menu__list {
  margin-right: 20px;
}

.navigation .menu__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.404);
}

.navigation .contacts {
  text-align: right;
}

.navigation .contacts__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.5);
}

.navigation .burger {
  display: none;
}

@media (max-width: 990px) {
  .navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .navigation .menu {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    flex-direction: column;
    padding: 50px 10px 30px 50px;
    width: 70%;
    height: 100vh;
    background: white;
    transform: translateX(-100%);
    transition: ease 0.5s;
  }
  .navigation .menu_active {
    transform: translateX(0);
    transition: ease 0.5s;
  }
  .navigation .menu__list {
    margin-right: 0;
    margin-bottom: 40px;
  }
  .navigation .menu__link {
    font-weight: 700;
    font-size: 15px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.404);
  }
  .navigation .contacts {
    display: none;
  }
  .navigation .burger {
    position: relative;
    display: inline-block;
    width: 28px;
    height: 25px;
    cursor: pointer;
  }
  .navigation .burger__line {
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 2px;
    width: 28px;
    height: 4px;
    background: #fff;
    transition: ease 0.5s;
  }
  .navigation .burger__line:nth-child(2) {
    top: 9px;
  }
  .navigation .burger__line:last-child {
    top: 18px;
  }
  .navigation .burger_active .burger__line {
    top: calc(50% - 4px);
    transform: rotate(45deg);
    transition: ease 0.5s;
  }
  .navigation .burger_active .burger__line:nth-child(2) {
    display: none;
  }
  .navigation .burger_active .burger__line:last-child {
    transform: rotate(-45deg);
    transition: ease 0.5s;
  }
}

.header {
  padding-top: 40px;
  padding-bottom: 110px;
  background: url("../img/header/header-bg.jpg") no-repeat;
  background-size: cover;
}

.header__title {
  margin-top: 90px;
}

.header__subtitle {
  margin-top: 25px;
}

.details {
  padding: 90px 0;
}

.details__title {
  margin-bottom: 19px;
  font-weight: 700;
  font-size: 27px;
  line-height: 29px;
  color: var(--color-black);
}

.details-video {
  position: relative;
  width: 100%;
  background-color: #f4f4fa;
}

.details-video .play {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  width: 104px;
  height: 104px;
  transform: translateX(-50%) translateY(-50%);
  cursor: pointer;
}

.details-video .play img {
  width: 100%;
  height: 100%;
}

.details-wrapper {
  display: flex;
  justify-content: space-between;
}

.details-form {
  padding-right: 10px;
  padding-left: 25px;
  max-width: 380px;
}

.details-form form {
  display: flex;
  flex-direction: column;
}

.details-form form input {
  margin-bottom: 30px;
}

@media (max-width: 425px) {
  .details-wrapper {
    flex-direction: column;
    align-items: center;
  }
  .details-video {
    width: 100%;
    height: 180px;
  }
  .details-video .play {
    width: 47px;
    height: 47px;
  }
  .details-form {
    margin-top: 24px;
    padding: 0;
  }
  .details-form button {
    width: 100%;
  }
}

.algorithm-wrapper {
  position: relative;
  border-radius: 5px;
  padding-top: 90px;
  padding-left: 105px;
  width: 100%;
  height: 318px;
  background: url("../img/header/header-bg.jpg");
}

.algorithm-wrapper::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 693px;
  height: 100%;
  background: url("../img/algorithm/algorithm-bg.png") no-repeat;
}

.algorithm__title {
  width: 265px;
  font-weight: 700;
  font-size: 44px;
  line-height: 45px;
  color: var(--color-white);
}

.algorithm__link {
  display: inline-block;
  margin-top: 17px;
  font-weight: 400;
  font-size: 14px;
  line-height: 29px;
  color: #ffffff38;
}

.algorithm__link span {
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  background: linear-gradient(-78deg, #ff40f8 0%, #cf97ff 35%, #40c7ff 70%, #40e6ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 425px) {
  .algorithm-wrapper {
    position: relative;
    padding: 36px 39px 10px 27px;
    height: 508px;
  }
  .algorithm-wrapper::after {
    position: absolute;
    top: inherit;
    bottom: 0;
    width: 100%;
    height: 370px;
    background: url("../img/algorithm-bg-mobile.png") no-repeat;
    background-size: cover;
  }
}

.system {
  margin-top: -160px;
  padding-top: 256px;
  padding-bottom: 80px;
  background-color: #f4f4fa;
}

.system-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
}

.system__item {
  margin-right: 50px;
  margin-bottom: 20px;
  min-width: 231px;
}

.system__item-number {
  position: relative;
  display: inline-block;
  font-weight: 800;
  font-size: 90px;
  background-image: linear-gradient(-51deg, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.system__item-number::after {
  content: "";
  position: absolute;
  left: 125px;
  top: 50%;
  display: block;
  width: 230px;
  height: 7px;
  background-image: url("../img/global/razd.png");
}

.system__item-title {
  margin-top: 2px;
  font-weight: 700;
  font-size: 30px;
}

.system__item-text {
  margin-top: 12px;
  max-width: 320px;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
}

.system__item:nth-child(3n) .system__item-number::after {
  display: none;
}

@media (max-width: 425px) {
  .system {
    margin-top: -250px;
    padding-top: 306px;
    padding-bottom: 86px;
  }
  .system-wrapper {
    margin-top: 50px;
  }
  .system__item {
    position: relative;
    margin-right: 0;
    margin-bottom: 36px;
  }
  .system__item::after {
    content: "";
    position: absolute;
    left: 25px;
    bottom: -38px;
    display: block;
    width: 7px;
    height: 33px;
    background-image: url("../img/system-line-mobile.png");
  }
  .system__item-number::after {
    display: none;
  }
  .system__item:last-child::after {
    display: none;
  }
}

.work {
  padding-top: 135px;
  padding-bottom: 120px;
}

.work-wrapper {
  margin-top: 50px;
}

.work-wrapper-head {
  position: relative;
  display: flex;
  padding-top: 35px;
  padding-left: 44px;
  height: 104px;
  background-image: url("../img/work-bg.png");
  background-size: cover;
}

.work-wrapper-head::after {
  content: "";
  position: absolute;
  right: 0;
  top: -14px;
  width: 120px;
  height: 120px;
  background-image: url("../img/dack.png");
  background-size: cover;
}

.work-wrapper-head__title {
  margin-right: 107px;
  width: 320px;
  font-weight: 700;
  font-size: 28px;
  color: var(--color-white);
}

.work-wrapper__item {
  display: flex;
  align-items: center;
  padding: 37px 43px 41px 45px;
  background-color: #f4f4fa;
  transition: ease-in 0.3s;
  cursor: pointer;
}

.work-wrapper__item_dark {
  background-color: #e4e5ee;
}

.work-wrapper__item h4 {
  width: 430px;
  font-weight: 600;
  font-size: 17px;
  line-height: 24px;
}

.work-wrapper__item p {
  max-width: 589px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.work-wrapper__item p a {
  background-image: linear-gradient(-51deg, #ff40f8 0, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.work-wrapper__item:hover {
  position: relative;
  overflow-x: hidden;
  box-shadow: 0 0 38px rgba(45, 75, 94, 0.26);
  transition: ease 0.5s;
}

.work-wrapper__item:last-child {
  position: relative;
  border-radius: 0 0 5px 5px;
}

.work-wrapper__item:last-child::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius: 0 0 5px 5px;
  width: 100%;
  height: 5px;
  background-image: linear-gradient(to left, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
}

.work-footer {
  position: relative;
  margin-top: 54px;
  border: 1px solid transparent;
  border-image: linear-gradient(-78deg, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  border-image-slice: 1;
  border-radius: 5px;
  padding-top: 43px;
  padding-bottom: 41px;
  padding-left: 45px;
}

.work-footer::after {
  content: "";
  position: absolute;
  right: 50px;
  top: 20px;
  width: 187px;
  height: 209px;
  background-image: url("../img/dack1.png");
}

.work-footer p {
  width: 760px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

@media (max-width: 425px) {
  .work .section-title span {
    display: none;
  }
  .work .container:nth-child(2) {
    padding: 0;
  }
  .work-wrapper {
    border-radius: 5px;
  }
  .work-wrapper-head {
    border-radius: 5px 5px 0 0;
    padding-top: 29px;
    padding-left: 15px;
  }
  .work-wrapper-head__title {
    font-weight: 700;
    font-size: 14px;
  }
  .work-wrapper__item {
    padding: 42px 10px 60px 17px;
  }
  .work-wrapper__item h4 {
    padding-right: 20px;
    max-width: 200px;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
  }
  .work-wrapper__item p {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
  }
  .work-footer {
    margin-bottom: 100px;
    padding: 28px 31px 129px 31px;
    height: 357px;
  }
  .work-footer::after {
    top: initial;
    bottom: -120px;
  }
  .work-footer p {
