  margin-top: 7px;
  margin-bottom: 46px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #808080;
}

.changes-item__title {
  display: inline-block;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 50px;
  background-image: linear-gradient(-77deg, #ff40f8 0, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.changes-item-list {
  width: 345px;
}

.changes-item-list__item {
  position: relative;
  margin-bottom: 39px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
}

.changes-item-list__item::before {
  content: "";
  position: absolute;
  left: -15px;
  top: 7px;
  border: 1px solid transparent;
  border-image: linear-gradient(-247deg, #ff40f8 0%, #a740ff 35%, #4045ff 70%, #40e6ff 100%);
  border-image-slice: 1;
  border-radius: 100%;
  width: 7px;
  height: 7px;
}

.changes-item-dack-block {
  position: relative;
  margin-top: 128px;
  border-radius: 5px;
  padding: 26px 10px 26px 29px;
  background-color: #e4e5ee;
}

.changes-item-dack-block::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  width: 137px;
  height: 114px;
  background-image: url("../img/changes-bg.png");
}

.changes-item-dack-block__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.5);
}

